<template>
  <div class="dt-main">
    <div id='serF'></div>
    <div class="dt-checkBox">
      <van-checkbox v-model="checked1"
                    @click="check1"
                    icon-size="15px">急救站</van-checkbox>
      <van-checkbox v-model="checked2"
                    @click="check2"
                    icon-size="15px">母婴室</van-checkbox>
      <van-checkbox v-model="checked3"
                    @click="check3"
                    icon-size="15px">观光车点</van-checkbox>
      <van-checkbox v-model="checked4"
                    @click="check4"
                    icon-size="15px">餐厅</van-checkbox>
      <van-checkbox v-model="checked5"
                    @click="check5"
                    icon-size="15px">特许餐厅</van-checkbox>
    </div>
  </div>
</template>
<script>
var map
var siteMarker
let infowindow
export default {
  props: ['mapCenter', 'firstAid', 'motherRoom', 'sightseeingCar', 'restaurant', 'spRestaurant'],
  data () {
    //这里存放数据
    return {
      checked1: true,
      checked2: false,
      checked3: false,
      checked4: false,
      checked5: false,
      childMapCenter: {},

      firstAidIDs: [],  // 急救站ids
      motherRoomIDs: [],  // 母婴室ids
      sightseeingCarIDs: [],  // 观光车ids
      restaurantIDs: [],    // 餐厅ids
      spRestaurantIDs: [],  // 特许餐厅ids
    };
  },
  //监控data中的数据变化
  watch: {
    mapCenter (newValue, oldValue) {
      this.childMapCenter = newValue
      this.$forceUpdate()
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.childMapCenter = this.mapCenter
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.initMap()
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () {
    this.setMapCenter()
  },
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发

  //方法集合
  methods: {
    initMap () {
      //定义地图中心点坐标
      var center = new TMap.LatLng(this.childMapCenter.lat, this.childMapCenter.lng)
      //定义map变量，调用 TMap.Map() 构造函数创建地图
      map = new TMap.Map(document.getElementById('serF'), {
        center: center,//设置地图中心点坐标
        zoom: 15.5,   //设置地图缩放级别
        viewMode: '2D'
      });
      // 初始化点标记
      siteMarker = new TMap.MultiMarker({
        id: 'Site',
        map: map,
        //样式定义
        styles: {
          //创建一个styleId为"myStyle"的样式（styles的子属性名即为styleId）
          "myStyle": new TMap.MarkerStyle({
            "width": 22,  // 点标记样式宽度（像素）
            "height": 26, // 点标记样式高度（像素）
            "src": require('../../assets/img/急救.png'),  //图片路径
            //焦点在图片中的像素位置，一般大头针类似形式的图片以针尖位置做为焦点，圆形点以圆心位置为焦点
            "anchor": { x: 12, y: 17 }
          }),
          "myStyleMR": new TMap.MarkerStyle({
            "width": 22,  // 点标记样式宽度（像素）
            "height": 26, // 点标记样式高度（像素）
            "src": require('../../assets/img/母婴室.png'),  //图片路径
            //焦点在图片中的像素位置，一般大头针类似形式的图片以针尖位置做为焦点，圆形点以圆心位置为焦点
            "anchor": { x: 12, y: 17 }
          }),
          "myStyleSi": new TMap.MarkerStyle({
            "width": 22,  // 点标记样式宽度（像素）
            "height": 26, // 点标记样式高度（像素）
            "src": require('../../assets/img/观光车点.png'),  //图片路径
            //焦点在图片中的像素位置，一般大头针类似形式的图片以针尖位置做为焦点，圆形点以圆心位置为焦点
            "anchor": { x: 12, y: 17 }
          }),
          "myStyleRe": new TMap.MarkerStyle({
            "width": 22,  // 点标记样式宽度（像素）
            "height": 26, // 点标记样式高度（像素）
            "src": require('../../assets/img/餐厅.png'),  //图片路径
            //焦点在图片中的像素位置，一般大头针类似形式的图片以针尖位置做为焦点，圆形点以圆心位置为焦点
            "anchor": { x: 12, y: 17 }
          }),
          "myStyleSp": new TMap.MarkerStyle({
            "width": 22,  // 点标记样式宽度（像素）
            "height": 26, // 点标记样式高度（像素）
            "src": require('../../assets/img/特许餐厅.png'),  //图片路径
            //焦点在图片中的像素位置，一般大头针类似形式的图片以针尖位置做为焦点，圆形点以圆心位置为焦点
            "anchor": { x: 12, y: 17 }
          }),
        },
        //点标记数据数组
        geometries: this.getGeometries()
      })
      //监听回调函数（非匿名函数）
      var clickHandler = function (evt) {
        if (infowindow) {
          //设置infoWindow
          infowindow.open(); //打开信息窗
          infowindow.setPosition(evt.geometry.position);//设置信息窗位置
          infowindow.setContent(evt.geometry.position.toString());//设置信息窗内容
          infowindow.close();//初始关闭信息窗关闭
        }
        var infoWindowLocation = evt.geometry.position;//创建一个坐标
        //创建InfoWindow实例，并进行初始化
        infowindow = new TMap.InfoWindow({
          map: map,
          content: evt.geometry.properties.title, //信息窗口内容
          position: infoWindowLocation,//显示信息窗口的坐标
          offset: { x: 0, y: -17 } //设置信息窗相对position偏移像素，为了使其显示在Marker的上方
        });
      }
      //监听marker点击事件
      siteMarker.on("click", clickHandler)
      // markerLayer.setGeometries([])
    },
    // 格式化点标记数据数组
    getGeometries () {
      let list = []
      for (let index in this.firstAid) {
        let market = this.firstAid[index]
        this.firstAidIDs.push(market.id)
        list.push({
          "id": market.id,   //点标记唯一标识，后续如果有删除、修改位置等操作，都需要此id
          "styleId": 'myStyle',  //指定样式id
          "position": new TMap.LatLng(market.lat, market.lng),  //点标记坐标位置
          "properties": {//自定义属性
            "title": market.name
          }
        })
      }
      return list
    },
    getGeometriesRM () {
      let list = []
      for (let index in this.motherRoom) {
        let market = this.motherRoom[index]
        this.motherRoomIDs.push(market.id)
        list.push({
          "id": market.id,   //点标记唯一标识，后续如果有删除、修改位置等操作，都需要此id
          "styleId": 'myStyleMR',  //指定样式id
          "position": new TMap.LatLng(market.lat, market.lng),  //点标记坐标位置
          "properties": {//自定义属性
            "title": market.name
          }
        })
      }
      return list
    },
    getGeometriesSC () {
      let list = []
      for (let index in this.sightseeingCar) {
        let market = this.sightseeingCar[index]
        this.sightseeingCarIDs.push(market.id)
        list.push({
          "id": market.id,   //点标记唯一标识，后续如果有删除、修改位置等操作，都需要此id
          "styleId": 'myStyleSi',  //指定样式id
          "position": new TMap.LatLng(market.lat, market.lng),  //点标记坐标位置
          "properties": {//自定义属性
            "title": market.name
          }
        })
      }
      return list
    },
    getGeometriesRe () {
      let list = []
      for (let index in this.restaurant) {
        let market = this.restaurant[index]
        this.restaurantIDs.push(market.id)
        list.push({
          "id": market.id,   //点标记唯一标识，后续如果有删除、修改位置等操作，都需要此id
          "styleId": 'myStyleRe',  //指定样式id
          "position": new TMap.LatLng(market.lat, market.lng),  //点标记坐标位置
          "properties": {//自定义属性
            "title": market.name
          }
        })
        // markerLayer.setGeometries([])
      }
      return list
    },
    getGeometriesSp () {
      let list = []
      for (let index in this.spRestaurant) {
        let market = this.spRestaurant[index]
        this.spRestaurantIDs.push(market.id)
        list.push({
          "id": market.id,   //点标记唯一标识，后续如果有删除、修改位置等操作，都需要此id
          "styleId": 'myStyleSp',  //指定样式id
          "position": new TMap.LatLng(market.lat, market.lng),  //点标记坐标位置
          "properties": {//自定义属性
            "title": market.name
          }
        })
      }
      return list
    },

    // 地图按钮
    check1 () {
      if (this.checked1) {
        siteMarker.add(this.getGeometries())
      } else {
        // siteMarker.setGeometries([])
        siteMarker.remove(this.firstAidIDs)
        if (infowindow) {
          infowindow.close();//初始关闭信息窗关闭
        }
      }
    },
    check2 () {
      if (this.checked2) {
        siteMarker.add(this.getGeometriesRM())
      } else {
        siteMarker.remove(this.motherRoomIDs)
        if (infowindow) {
          infowindow.close();//初始关闭信息窗关闭
        }
      }
    },

    check3 () {
      if (this.checked3) {
        siteMarker.add(this.getGeometriesSC())
      } else {
        //siteMarker.setGeometries([])
        siteMarker.remove(this.sightseeingCarIDs)
        if (infowindow) {
          infowindow.close();//初始关闭信息窗关闭
        }
      }
    },
    check4 () {
      if (this.checked4) {
        siteMarker.add(this.getGeometriesRe())
      } else {
        //siteMarker.setGeometries([])
        siteMarker.remove(this.restaurantIDs)
        if (infowindow) {
          infowindow.close();//初始关闭信息窗关闭
        }
      }
    },
    check5 () {
      if (this.checked5) {
        siteMarker.add(this.getGeometriesSp())
      } else {
        siteMarker.remove(this.spRestaurantIDs)
        if (infowindow) {
          infowindow.close();//初始关闭信息窗关闭
        }
      }
    },

    setMapCenter () {
      var center = new TMap.LatLng(this.childMapCenter.lat, this.childMapCenter.lng);
      map.setCenter(center)
    }
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import "../../styles/dimens";
.dt-main {
  position: relative;
  width: 100%;
  height: 100%;
  #serF {
    width: 100%;
    height: 100%;
  }
  .dt-checkBox {
    position: absolute;
    display: inline-block;
    z-index: 9999;
    right: 0;
    bottom: 0;
    .van-checkbox {
      float: right;
      font-size: px2rem(15);
      margin-right: px2rem(5);
      margin-top: px2rem(5);
      z-index: 2000;
      color: #000000;
      font-weight: 400;
      font-family: "PingFang-SC-Regular";
    }
  }
}
/deep/ .dt-checkBox .van-checkbox__label {
  font-size: px2rem(15);
  color: #000000;
  font-weight: 400;
  font-family: "PingFang-SC";
}
</style>